<template>
  <div class="content">

    <center>
          <input-card ref="inputCard" placeholder="Hedera Account No" @click:search="onClickSearch" />
    </center>

    <!-- {{nftList}}

    {{tokenBalances}} -->

    <!-- {{temp.length}}

    {{ftList}}

    {{nftList}} -->

    <md-progress-bar v-if="this.isLoading" md-mode="indeterminate"></md-progress-bar>
    <div class="md-layout">

      <!-- Non-fungible -->
      <div v-for="nft of nftList" :key="nft.assetURL" class="md-layout-item md-medium-size-100 md-xsmall-size-100 md-size-20">
        <nft-card
          :assetURL="nft.assetURL"
          :name="nft.metadataInfo.name"
          :description="nft.metadataInfo.description"
          :tokenId="nft.token_id"
          :serial="nft.serial_number"
          :type="nft.type" />
      </div>

      <!-- Fungible -->
      <div v-for="ft of ftList" :key="ft.assetURL" class="md-layout-item md-medium-size-100 md-xsmall-size-100 md-size-20">
        <nft-card
          :assetURL="ft.assetURL"
          :name="ft.name"
          :description="ft.description"
          :tokenId="ft.tokenId"
          :balance="tokenBalances.filter(i=> i.token_id == ft.tokenId)[0].balance"
          :type="ft.type" />
      </div>

    </div>

    <!-- Empty state content -->
    <md-empty-state v-if="this.nftList.length == 0 && this.ftList.length == 0"
      class="md-primary"
      md-icon="search"
      md-label="Ready!"
      md-description="Enter a valid Hedera account number and click Search to see all the NFTs belongs to that account here.">
    </md-empty-state>

    <md-snackbar md-position="center" :md-duration="3000" :md-active.sync="showSnackbar" md-persistent>
      <span>{{this.snackbarMessage}}</span>
      <md-button class="md-primary" @click="showSnackbar = false">OK</md-button>
    </md-snackbar>
  </div>
</template>

<script>
import {
  NFTCard,
  InputCard
} from "@/components";
import NFTService from '../service/NFTService';
import CommonService from '../service/CommonService';

export default {
  components: {
    'nft-card': NFTCard,
    'input-card': InputCard
  },
  data() {
    return {
      nftService: new NFTService(),
      commonService: new CommonService(),
      tokenList: [],
      tokenBalances: [],
      nftList: [],
      ftList: [],
      accountId: 'x',
      isLoading: false,
      showSnackbar: false,
      snackbarMessage: '',
      temp: []
    };
  },
  methods: {
    fetchTokenList: function(accountId) {
      if (accountId && accountId != '') {
        // Get token balances list.
        this.nftService.getTokenBalances(accountId).then(bals => {
          this.tokenBalances = bals.balances[0].tokens.filter(i => i.balance > 0);
          console.log(this.tokenBalances)
          // For each token.
          this.tokenBalances.forEach(i => {
            // Get each token info.
            this.nftService.getTokenInfo(i.token_id).then(tInfo => {
              console.log(tInfo)
              this.temp.push(tInfo)
              if (tInfo.type == 'FUNGIBLE_COMMON') {
                // If Fungible token.
                this.nftService.getGomintMetadata(tInfo.token_id).then(data => {
                  console.log('gomintdata', data)
                  this.ftList.push({
                    name: tInfo.name,
                    description: data.tokenDescription,
                    assetURL: data.tokenAssetURL,
                    tokenId: i.token_id,
                    type: 'FUNGIBLE_COMMON'
                  })
                })
              } else if (tInfo.type == 'NON_FUNGIBLE_UNIQUE') {
                // If Non-fungible token.
                this.nftService.getNFTList(accountId, tInfo.token_id).then(data => {
                  if (data.nfts != undefined) {
                    data.nfts.forEach(j => {
                      this.resolveNFTMetadata(j)
                    })
                  } else {
                    console.log('Error: Invalid response')
                  }
                });
              }
            })
          })
        })
      }
    },

    resolveNFTMetadata: function(nft) {
      var cid = this.commonService.base64decode(nft.metadata)
      // console.log('metadata decoded', cid)

      var url = this.commonService.generateEmbedURL(cid)
      console.log('generated url', url)
      this.nftService.getMetadata(url).then(data => {
        if (data.name != undefined) {
          nft.metadataInfo = data
          nft = this.commonService.parseNFTObject(nft)
          
          var embedURL = this.commonService.generateEmbedURL(nft.metadataInfo.image)
          this.commonService.getHeaders(embedURL).then(res => {
            if (res['content-type'] == 'text/html') {
              this.commonService.getFileURLFromIPFSDirPage(embedURL).then(uri => {
                nft.assetURL = this.commonService.generateEmbedURL(uri)
              })
            } else {
              nft.assetURL = embedURL
            }
          })

          nft.type = 'NON_FUNGIBLE_UNIQUE'
          this.nftList.push(nft)
        } else {
          console.log('Error: Error metadata at', url)
        }
      })
    },
    searchNFT(accountId) {
      this.tokenList = []
      this.nftList = []
      this.ftList = []
      if (accountId.split('.').length == 3) {
        this.accountId = accountId.trim()
        this.isLoading = true
        this.showSnackbar = false;
        this.fetchTokenList(accountId.trim())
      } else {
        this.accountId = ''
        this.snackbarMessage = "Please enter a valid Hedera account number.(Ex: 0.0.12345)"
        this.showSnackbar = true
      }
    },
    onClickSearch(accountId) {
      if (accountId && accountId != '') {
        location.href = `?accountId=${accountId}`
      } else {
        location.href = `/`
      }
    }
  },
  mounted() {
    // this.$refs.inputCard.accountId = this.$route.params.accountId
    this.accountId = this.$route.params.accountId
    // this.fetchTokenList(this.$route.query.accountId);
    if (this.$route.query.accountId) {
      this.$refs.inputCard.accountId = this.$route.query.accountId
      this.searchNFT(this.$route.query.accountId)
    }
  }
};
</script>
