const request = require('request');
import SocketService from './SocketService'


export default class CommonService {
    base64decode(string) {
        return Buffer.from(string, 'base64').toString()
    }

    generateEmbedURL(text) {
        var chunks = text.split('/')
        if (chunks.length == 1) {
            // If CID
            return `https://ipfs.io/ipfs/${text}`
        } else if (chunks[0] == 'ipfs:') {
            // If IPFS URI
            return `https://ipfs.io/ipfs/${chunks[2]}/${chunks[3]}`
        } else {
            // Other URL
            return text
        } 
    }

    parseNFTObject(obj) {
        if (typeof obj.metadataInfo.description == 'object') {
            obj.metadataInfo.description = obj.metadataInfo.description.description
        }
        if (typeof obj.metadataInfo.image == 'object') {
            obj.metadataInfo.image = obj.metadataInfo.image.description
        }
        return obj
    }

    getFileURLFromIPFSDirPage(url) {
        const socket = new SocketService()
        return socket.getPage().get(url).then(res => {
            var pageContent = res.data
            var ipfsPath = pageContent.split(`<a class="ipfs-hash" href="`)[1].split(`"`)[0]
            return url.split('/ipfs/')[0] + ipfsPath // Join baseURL to ipfs path
        })
    }

    getHeaders(url) {
        const socket = new SocketService()
        return socket.getHeaders(url).then(res => res.headers);
    }
 }
